import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import LoginPage from '../views/LoginPage.vue';

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  { 
    path: '/logina',
    name: 'LoginPage',
    component: LoginPage
  }
];

const router = new VueRouter({
  routes
});

